// @ts-nocheck
import React, { useContext, useState } from 'react'
import { Link } from "react-router-dom"
import { Container1, Container2, Container3, H3nav } from "./Header.styled"
import { AuthContext } from '../../context/AuthContext'

export const Header = () => {
  const { user, dispatch } = useContext(AuthContext)

  const handleLogout = async (e) => {
    e.preventDefault()
    dispatch({type:"LOGOUT"})
  };

  return (
    <Container1>
        <Container2>
            <Container3>
                <h1>Patrick <span style={{color: "red"}}>EN</span></h1>
            </Container3>
            <Container3 style={{width: "60%", maxwidth: "1024px", justifyContent: "flex-end"}}>
                <H3nav>
                  <Link to="/" style={{color: "black", textDecoration: "none"}}>
                    Home
                  </Link>
                </H3nav>
                {false && <H3nav>
                  <Link to="/tabelle" style={{color: "black", textDecoration: "none"}}>
                    Tabellen-Tippspiel
                  </Link>
                </H3nav>}
                {false && <H3nav>Kontakt</H3nav>}
                {user && <><H3nav>
            <Link to="/tipps" style={{ color: "black", textDecoration: "none" }}>
              Spieltag-Tippspiel
            </Link>
          </H3nav><H3nav onClick={handleLogout}>
              <Link to="/" style={{ color: "black", textDecoration: "none" }}>
                Abmelden
              </Link>
            </H3nav></>}
                {!user && <H3nav>
                  <Link to="/anmelden" style={{color: "black", textDecoration: "none"}}>
                    Anmelden
                  </Link>
                </H3nav>
                }
            </Container3>
        </Container2>
    </Container1>
  )
}
