// @ts-nocheck
import styled from "styled-components";

export const Body1 = styled.div`
    position: relative;
    top: 0;
    height: 100vh;
    background-image: linear-gradient(360deg, #222, rgba(34, 34, 34, 0)), url(${(props) => props.img});
    background-size: cover;
    background-position: 0px 0px, 50% 50%;
    position: relative;
    z-index: 0;
    margin: auto;
`
export const Bodyrest = styled.div`
    height: 3700px;
    background-color: #000e14;;;
`
export const Bodyrest2 = styled.div`
    flex: 1 1 auto;
`
export const Body2 = styled.div`
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
`

export const Bodyh1 = styled.h1`
    color: whitesmoke;
    font-size: 100px;
`
export const Bodybutton = styled.button`
    padding: 10px 25px;
    margin-top: 30px;
    border-radius: 5px;
    border: none;
    background-color: #C53939;
    font-size: 40px;
    color: white;
    &:hover {
        color: #C53939;
        cursor: pointer;
        transform: scale(1.1);
        background-color: white;
    }
`
export const Bodybuttonwh = styled.button`
    width: 100%;
    padding: 10px 25px;
    border: none;
    background-color: #C53939;
    font-size: 40px;
    color: white;
    border-radius: 5px;
    &:hover {
        color: #C53939;
        cursor: pointer;
        background-color: white;
    }
`
export const Yesbutton = styled.button`
    width: 20%;
    padding: 10px 15px;
    border: none;
    background-color: #C53939;
    font-size: 20px;
    color: white;
    border-radius: 5px;
    margin-bottom: 10px;
    &:hover {
        color: #C53939;
        cursor: pointer;
        background-color: white;
    }
`